import { FunctionComponent, ChangeEvent, useState } from 'react'
import classnames from 'classnames'
import { Link } from 'react-router-dom'
import { useHistory } from 'react-router-dom'
import HeaderNav from '../common/header-nav/HeaderNav'
import FormField from '../common/form-field/FormField'
import Button from '../common/button/Button'
import { File, Platform, PlatformText } from '../../types'
import Alert from '../common/alert/Alert'
import { uploadFile, uploadFiles } from '../../redux/actions/ui'
import { useDispatch } from 'react-redux'
import bridge from 'smartapp-bridge/build/main/index'
import { TYPES, HANDLER } from '../../constants'

interface Props {
  handleGoToConfirm:any,
  setPlatform: any,
  setDescription: any,
  setDetailInfo: any,
  platform?: Platform,
  description?: string,
  details?: string,
  attachments?: any,
  setAttachments?: any,
}

export const bytesToSize = (bytes: number) => {
  const sizes = [
    'Bytes',
    'Kb',
    'Mb',
    'Gb',
    'Tb',
  ]

  if (bytes === 0) return `0 ${sizes[0]}`

  const sizeIdx = Math.floor(Math.log(bytes) / Math.log(1024))
  const size = Math.round(bytes / Math.pow(1024, sizeIdx))

  return `${size} ${sizes[sizeIdx]}`
}

const IssueNew:FunctionComponent<Props> = ({
  handleGoToConfirm,
  setPlatform,
  setDescription,
  setDetailInfo,
  platform,
  description = '',
  details = '',
  attachments,
}) => {
  const formFieldDescription = `По возможности укажите:
<ul>
<li>Версию операционной системы</li>
<li>Модель устройства</li>
<li>Версию приложения eXpress (можно посмотреть в настройках – о программе)</li>
</ul>`

  const [isShowCancelAlert, setIsShowCancelAlert] = useState<boolean>(false)
  const history = useHistory()
  const dispatch = useDispatch()

  const handleChangePlatform = (value: any) => {
    setPlatform(value.value)
  }

  const handleChangeDescription = (value: any) => {
    setDescription(value)
  }

  const handleChangeDetails = (value: any) => {
    setDetailInfo(value)
  }

  const handleUploadFile = () => {
    dispatch(uploadFile('image'))
  }

  const handleUploadFiles = () => {
    dispatch(uploadFiles('image'))
  }

  const handleCancelClick = () => {
    if (description || details) setIsShowCancelAlert(true)
  }

  const handleOpenFile = (file: File) => {
    bridge.send({
      type: TYPES.OPEN_FILE,
      handler: HANDLER.EXPRESS,
      payload: file
    })
   }

  const cancelLinkPath = description || details ? '/ticket-new' : '/tickets'

  const platforms = Object.values(Platform)
  
  const options = platforms.map(platform => {
    return { label: PlatformText[platform], value: platform }
  })

  const disabled = !description || !platform

  return (
    <div className="app-layout">
      <header className="app-layout__header">
        <HeaderNav>
          <Link className="header-nav-btn header-nav-btn--icon" to={cancelLinkPath} onClick={handleCancelClick}>
            <svg
              className="header-nav-btn__icon"
              viewBox="0 0 18 18"
              width="18"
              height="18"
              fill="none"
              xmlns="http://www.w3.org/2000/svg"
            >
              <path
                d="M11 9l6.4-6.5a1 1 0 000-1.3l-.6-.6a1 1 0 00-.6-.3 1 1 0 00-.7.3L9 7 2.5.6a1 1 0 00-.7-.3 1 1 0 00-.6.3l-.6.6a1 1 0 000 1.3L7 9 .6 15.5a1 1 0 00-.3.7c0 .2.1.5.3.6l.6.6c.1.2.4.3.6.3.3 0 .5-.1.7-.3L9 11l6.5 6.5c.2.2.4.3.7.3.2 0 .5-.1.6-.3l.6-.6c.2-.1.3-.4.3-.6a1 1 0 00-.3-.7L11 9z"
                fill="currentColor"
              />
            </svg>
          </Link>
          <strong className="header-nav__title">Создать</strong>

          <span 
            className={classnames({
              'header-nav-btn__text': true,
              'header-nav-btn': true,
              'header-nav-btn__disabled': disabled,
            })} 
            onClick={() => disabled ? null : handleGoToConfirm(null)}>
              Дальше
          </span>
        </HeaderNav>
      </header>

      <main className="app-layout__main">
        <div className="app-form">
          <FormField type="dropdown" value={platform} placeholder="Платформа" onChange={handleChangePlatform} options={options} />

          <FormField
            type="text"
            placeholder="Описание"
            value={description}
            onChange={(e:ChangeEvent<HTMLTextAreaElement>) => handleChangeDescription(e.target.value)}
          />

          <h3 className="app-title app-title--h3 app-form__divider">Дополнительная информация</h3>
          <FormField
            type="text"
            placeholder="Описание"
            description={formFieldDescription}
            value={details}
            onChange={(e:ChangeEvent<HTMLTextAreaElement>) => handleChangeDetails(e.target.value)}
          />

          <h3 className="app-title app-title--h3 app-form__divider">Вложения</h3>
          {attachments.map((attachment: File, index: number) => (
            <div className="app-file" key={index} onClick={()=> handleOpenFile(attachment)}>
              <div className="app-file__title">{attachment?.fileName}</div>
              <div className="app-file__size">{bytesToSize(attachment?.fileSize)}</div>
          </div>
          ))}

          <Button variant="primary" type="outline" onClick={handleUploadFile}>
            Прикрепить файл
          </Button>

          <Button variant="primary" type="outline" onClick={handleUploadFiles}>
            Прикрепить файлы
          </Button>
        </div>

        {isShowCancelAlert && <Alert>
          <h2 className="app-title app-title--h4">Отменить создание обращения</h2>
          <p className="app-text app-text--divider">
            Вы уверены, что хотите отменить создание обращения? Введенные данные не сохранятся.
          </p>
          <footer className="alert__footer">
            <Button variant="primary" type="outline" onClick={() => setIsShowCancelAlert(false)}>
              Назад
            </Button>
            <Button variant="alert" type="default" onClick={() => history.push('/tickets')}>
              Отменить
            </Button>
          </footer>
        </Alert>}
      </main>
    </div>
  )
}

export default IssueNew